body{margin:0;font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;background-image:url(/ualuck/static/media/bg.9d9b8813.jpg);background-size:cover;background-repeat:no-repeat;background-position:center center}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.row{max-width:1230px;margin:0 auto}.row{position:relative;min-height:100vh}

h1,h2,h3,h4,h5,h6,p,a,span{padding:0;margin:0}p{color:#fff}

h1,h2,h3,h4,h5,h6,p,a,span{padding:0;margin:0}p{color:#fff}.logo{display:flex;padding-top:40px}.logo a{text-decoration:none;color:white;margin-right:20px}.logo p{font-family:'Ubuntu', sans-serif;font-size:15px;font-weight:700;font-style:italic;line-height:1.17;letter-spacing:0.90px;color:#ffffff}.logo .second-p{font-weight:normal}.logo .slogan{padding:5px}.logo .yellow{color:yellow}.logo .blue{color:#00b7ff}

h1,h2,h3,h4,h5,h6,p,a,span{padding:0;margin:0}p{color:#fff}.main{display:flex;flex-direction:row;margin:0 auto;position:absolute;top:20%;left:12%}.main .registration-block{align-self:center}.main p{font-size:43px;font-weight:bold;letter-spacing:1px;line-height:50px}.main span{color:#00b7ff;font-size:63px;letter-spacing:0px}.main .buttons-place{margin-top:20px}.main .buttons-place .button{display:inline-block;background-color:rgba(255,255,255,0.1);padding:10px 20px;cursor:pointer}.main .buttons-place .button span{font-size:14px;font-weight:bold;font-style:normal;line-height:normal;letter-spacing:0.61px;text-align:center;color:#ffffff}.main .buttons-place .button:last-child{border-top-right-radius:100px;border-bottom-right-radius:100px}.main .buttons-place .button:first-child{border-top-left-radius:100px;border-bottom-left-radius:100px}.main .buttons-place .button:hover{background-color:#00b7ff}.main .apply{margin:15px 0 28px 0}.main .apply input{width:332px;height:48px;padding:0;border:none;border-radius:6px;font-size:14px;font-weight:bold;text-indent:10px}.main .apply input:focus{outline:none}.main .promocode{padding-bottom:40px}.main .promocode span{color:#fff;font-size:14px;letter-spacing:0.61px;font-weight:bold}.main .promocode .promocode-button{background-color:#00b7ff;display:inline-block;height:25px;width:25px;border-radius:50%;margin-left:10px;text-align:center;font-size:16px;cursor:pointer}.main .agreement-text{margin-left:30px;max-width:300px}.main .agreement-text p{font-size:10px;line-height:10px;letter-spacing:0.44px;font-weight:normal;font-stretch:normal;font-style:normal}.main .agreement-text span{font-size:10px;letter-spacing:0.44px;color:#fff;text-decoration:underline}.main .agreement{display:flex}.main .container{display:block;position:relative;cursor:pointer;font-size:22px;-webkit-user-select:none;-moz-user-select:none;-ms-user-select:none;user-select:none}.main .container input{position:absolute;opacity:0;cursor:pointer;height:0;width:0}.main .container .checkmark{position:absolute;top:0;left:0;height:25px;width:25px;background-color:#eee}.main .container .container:hover input ~ .checkmark{background-color:#ccc}.main .container input:checked ~ .checkmark{background-color:#00b7ff}.main .container .checkmark:after{content:"";position:absolute;display:none}.main .container input:checked ~ .checkmark:after{display:block}.main .container .checkmark:after{left:9px;top:5px;width:5px;height:10px;border:solid white;border-width:0 3px 3px 0;-webkit-transform:rotate(45deg);transform:rotate(45deg)}.main .sign-up{padding-top:40px;display:flex}.main .sign-up .button{padding:14px 30px;font-weight:800;font-style:normal;line-height:normal;background-color:#00b7ff;border-radius:43px;box-shadow:0 24px 41px 0 rgba(0,183,255,0.19);cursor:pointer}.main .sign-up span{color:#fff;font-size:14px;letter-spacing:1px;text-transform:uppercase}.main .sign-up .or{font-weight:bold;font-size:15px;padding:14px 12px}.main .sign-up .log-in{padding:13px 12px}.main .sign-up .log-in span{font-size:15px;font-weight:bold;color:#00b7ff;cursor:pointer}.main .img-place{background-image:url(/ualuck/static/media/soccer-player.d3bcb63c.png);background-size:100%;background-repeat:no-repeat;background-position-y:30%;width:500px;height:590px}

.footer{margin-bottom:20px;display:flex;justify-content:space-between;position:absolute;bottom:0;width:100%}.footer .quality{display:flex}.footer .quality .standard-mark{background-image:url(/ualuck/static/media/standard.4c7ab30d.png);background-size:cover;background-position:center center;background-repeat:no-repeat;width:50px;height:50px;margin:0;padding:0;margin-right:10px}.footer .quality .verified{color:#ffcd00}.footer .quality span{color:white;display:block;font-size:16px;letter-spacing:-0.1px}.footer .payment{background-image:url(/ualuck/static/media/icons.758b153b.png);background-size:100%;background-repeat:no-repeat;background-position-y:65%;width:60%;height:auto}

